import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userProperties: {
      userID: "",
      userRole: ""
    },
    caseProperties: {
      caseID: "",
      caseType: "",
      manageMode: ""
    }
  },
  mutations: {
    storeUserProperties (state, payload) {
      state.userProperties = payload;
    },
    storeCaseProperties (state, payload) {
      state.caseProperties = payload;
    }
  },
  actions: {
    setUserProperties (context, payload) {
      context.commit("storeUserProperties", payload);
    },
    setCaseProperties (context, payload) {
      context.commit("storeCaseProperties", payload);
    }
  },
  getters: {
    getUserProperties: state => {
      return state.userProperties;
    },
    getCaseProperties: state => {
      return state.caseProperties;
    }
  },
  modules: {}
});
