/* eslint-disable */
import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "/",
    component: () => import("../components/Home.vue")
  },
  {
    path: "/profile",
    name: "profile",
    component: () => import("../components/auth/Profile.vue")
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../components/Login.vue")
  },
  {
    path: "/register",
    name: "register",
    component: () => import("../components/Register.vue")
  },
  {
    path: "/mainuser",
    name: "mainuser",
    component: () => import("../components/MainUser.vue")
  },
  {
    path: "/createReport_fakeAds",
    name: "createReport_fakeAds",
    component: () => import("../components/CreateReport_FakeAds.vue")
  },
  {
    path: "/createReport_product",
    name: "createReport_product",
    component: () => import("../components/CreateReport_Product.vue")
  },
  {
    path: "/createReport_symptom",
    name: "createReport_symptom",
    component: () => import("../components/CreateReport_Symptom.vue")
  },

];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
