            v-if="key == 3"
            outlined
            rounded
            text
            color="#D9534F"
            @click="logout"
          >
            <span class="header_menu_hitText">{{ value.title }}</span>
          </v-btn>
          <v-btn
            v-if="key == 4"
            outlined
            rounded
            text
            color="#00B5F8"
            :to="value.actionLink"
          >
