      <v-list nav>
        <v-list-item
          v-for="header in headers_menu_reporter"
          :key="header.title"
          :to="header.actionLink"
          link
        >
          <v-list-item-icon>
            <v-icon class="ml-3" color="primary">{{ header.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ header.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </div>

  <div v-else-if="auth == 'logged_in' && role == '3'">
    <v-app-bar app flat color="white" height="90">
      <v-row align="center" justify="center" class="mt-4">
        <div class="d-flex align-center ml-5">
          <router-link to="/">
            <v-img
              transition="scale-transition"
              origin="center center"
              src="http://www.tawainetwork.org/assetmaterials/logo.png"
              class="my-3"
              contain
              max-height="60"
              max-width="60"
            />
          </router-link>
        </div>

        <div
          v-for="(value, key) in headers_menu_admin"
          :key="value.title"
          class="hidden-sm-and-down ml-5"
        >
          <v-btn v-if="key < 2" rounded text :to="value.actionLink">
            <span class="header_menu_text">{{ value.title }}</span>
          </v-btn>
        </div>

        <v-spacer></v-spacer>

        <div
          v-for="(value, key) in headers_menu_admin"
          :key="key"
          class="hidden-sm-and-down ml-5"
        >
          <v-btn v-if="key > 1 && key < 4" rounded text :to="value.actionLink">
            <span class="header_menu_text">{{ value.title }}</span>
          </v-btn>
          <v-btn
            v-if="key == 4"
            outlined
            rounded
            text
            color="#D9534F"
            @click="logout"
          >
            <span class="header_menu_hitText">{{ value.title }}</span>
          </v-btn>
          <v-btn
