            :to="value.actionLink"
          >
            <span class="header_menu_hitText">{{ value.title }}</span>
          </v-btn>
        </div>

        <div class="hidden-md-and-up mr-5">
          <v-btn icon @click="navigationDrawer = !navigationDrawer">
            <v-icon>mdi-menu</v-icon>
          </v-btn>
        </div>
      </v-row>
    </v-app-bar>
    <v-navigation-drawer
      v-model="navigationDrawer"
      app
      floating
      temporary
      right
    >
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title>
            <v-container class="fill-height" fluid>
              <v-row class="ml-1 mt-1" align="center" justify="left">
                TaWai (ตาไว)
                <v-btn
                  class="ml-10"
                  rounded
                  outlined
                  text
                  color="black"
                  @click.stop="navigationDrawer = !navigationDrawer"
                >
                  <v-icon> mdi-chevron-right </v-icon>
                </v-btn>
              </v-row>
            </v-container>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list nav>
        <v-list-item
          v-for="header in headers_menu_admin"
          :key="header.title"
          :to="header.actionLink"
          link
        >
          <v-list-item-icon>
            <v-icon class="ml-3" color="primary">{{ header.icon }}</v-icon>
