            <v-icon>mdi-menu</v-icon>
          </v-btn>
        </div>
      </v-row>
    </v-app-bar>
    <v-navigation-drawer
      v-model="navigationDrawer"
      app
      floating
      temporary
      right
    >
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title>
            <v-container class="fill-height" fluid>
              <v-row class="ml-1 mt-1" align="center" justify="left">
                TaWai (ตาไว)
                <v-btn
                  class="ml-10"
                  rounded
                  outlined
                  text
                  color="black"
                  @click.stop="navigationDrawer = !navigationDrawer"
                >
                  <v-icon> mdi-chevron-right </v-icon>
                </v-btn>
              </v-row>
            </v-container>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list nav>
        <v-list-item
          v-for="header in headers_menu"
          :key="header.title"
          :to="header.actionLink"
          link
        >
          <v-list-item-icon>
            <v-icon class="ml-3" color="primary">{{ header.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ header.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
import EventBus from "./EventBus";
export default {
  data: () => ({
    auth: "",
    role: "",
    navigationDrawer: false,
    headers_menu: [
      { icon: "mdi-newspaper", title: "บทความ", actionLink: "" },
      { icon: "mdi-marker-check", title: "ตรวจสอบผลิตภัณฑ์", actionLink: "" },
      { icon: "mdi-information", title: "ช่วยเหลือ", actionLink: "" },
      { icon: "mdi-pill", title: "รู้จักตาไว", actionLink: "" },
      { icon: "mdi-login", title: "เข้าสู่ระบบ", actionLink: "login" }
    ],
    headers_menu_reporter: [
      { icon: "mdi-newspaper", title: "บทความ", actionLink: "" },
      { icon: "mdi-marker-check", title: "ตรวจสอบผลิตภัณฑ์", actionLink: "" },
      {
        icon: "mdi-view-quilt",
        title: "หน้าผู้ใช้งานหลัก",
        actionLink: "mainuser"
      },
      { icon: "mdi-logout", title: "ออกจากระบบ", actionLink: "" }
    ],
    headers_menu_admin: [
      { icon: "mdi-newspaper", title: "บทความ", actionLink: "" },
      { icon: "mdi-marker-check", title: "ตรวจสอบผลิตภัณฑ์", actionLink: "" },
      {
        icon: "mdi-view-quilt",
        title: "หน้าผู้ใช้งานหลัก",
        actionLink: "mainuser"
      },
      { icon: "mdi-view-dashboard", title: "ระบบการจัดการ", actionLink: "" },
      { icon: "mdi-logout", title: "ออกจากระบบ", actionLink: "" }
    ]
  }),
  methods: {
    checkUserAuth_local() {
      let checkRole = localStorage.getItem("userRole");
      if (checkRole) {
        this.auth = "logged_in";
        this.role = checkRole;
      }
    },
    logout() {
      // let tempToken = localStorage.getItem('userToken')
      localStorage.removeItem("userToken");
      localStorage.removeItem("userID");
      localStorage.removeItem("userRole");
      this.auth = "";
      this.role = "";
      console.log(
        "LOGOUT.CONFIRM - CLEAR DATA: auth> ",
        this.auth,
        ", role> ",
        this.role
      );
      this.$router.push("/");
      // Call API Backend to update signin status to 0
    }
  },
  mounted() {
    this.checkUserAuth_local();
    EventBus.$on("userACCESS", userACCESS => {
      // console.log('User.ACCESS_Detailed (EventBus): ', userACCESS)
      this.auth = userACCESS.status;
      this.role = userACCESS.role;
    });
  }
};
</script>

<style scoped>
.header_menu_text {
  font-weight: 400;
  font-size: 18px;
  color: #414141;
}
.header_menu_hitText {
  font-weight: 400;
  font-size: 18px;
}
</style>
